.examinevote-details .details-card {
  width: 853px;
  height: 1050px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  padding: 32px 36px;
}
.examinevote-details .details-card .details-box,
.examinevote-details .details-card .details-big-box {
  width: 781px;
  background: #f5f5f5;
  border-radius: 4px;
  margin-bottom: 24px;
  padding: 0 17px;
}
.examinevote-details .details-card .details-box .box-span,
.examinevote-details .details-card .details-big-box .box-span {
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
}
.examinevote-details .details-card .details-box {
  height: 55px;
  line-height: 55px;
}
.examinevote-details .details-card .details-big-box {
  padding: 30px 17px;
}
.examinevote-details .details-card .radio-form {
  padding: 10px 16px;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}
.examinevote-details .details-card .ant-form-item {
  margin-bottom: 0;
}
